/*BODY*/
body {
  font-size: 20px;
  font-weight: 400;
  font-family: "Inter", sans-serif;
  line-height: 140%;
  color: #101010;
  margin: 0px;
}

@media (max-width: 680px) {
  body {
    font-size: 16px;
    line-height: 140%;
  }
}
/*CONTENER*/
.contener {
  max-width: 680px;
  margin: 0 auto;
  padding: 0px 16px;
}

/*LINK*/
.cs-link {
  color: #2563EB;
  text-decoration: none;
}
.cs-link:hover {
  text-decoration: underline;
}

.cs-link-dark {
  color: #101010;
}

.title-span {
  color: #7B7B7C;
}

.case-title {
  font-size: 28px;
  color: #000;
  line-height: 100%;
  font-weight: 500;
  margin: 0px 0px 32px 0px;
}

@media (max-width: 680px) {
  .case-title {
    font-size: 20px;
    margin: 0px 0px 24px 0px;
  }
}
/*LIST ITEM*/
.list__item {
  display: flex;
  gap: 30px;
}
.list__item .akapit {
  display: block;
  margin-bottom: 8px;
}
.list__item .list__number {
  font-size: 16px;
}
@media (max-width: 680px) {
  .list__item .list__number {
    font-size: 14px;
  }
}
.list__item .list__text--bottom {
  border-left: 2px solid rgba(17, 0, 0, 0.08);
  color: #7B7B7C;
  padding-left: 12px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-top: 12px;
}

@media (max-width: 680px) {
  .list__item {
    gap: 16px;
  }
}
.header {
  padding: 8px 0px;
  border-bottom: 1px solid rgba(15, 27, 94, 0.08);
  position: fixed;
  top: 0;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.88);
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
  font-size: 16px;
}

.header__nav-wrapper {
  display: flex;
  justify-content: space-between;
}

.hero {
  height: 100vh;
}
.hero .contener {
  height: 94%;
}
.hero .hero__wrapper {
  max-width: 528px;
  display: flex;
  flex-direction: column;
  gap: 32px;
  justify-content: flex-end;
  height: inherit;
}
.hero .hero__wrapper .hero__title {
  font-size: 36px;
  line-height: 130%;
  font-weight: 500;
  margin-bottom: 16px;
}
.hero .hero__wrapper .hero__title span {
  color: #7B7B7C;
}
@media (max-width: 580px) {
  .hero .hero__wrapper .hero__title {
    font-size: 6vw;
  }
}
.hero .hero__wrapper .hero__text span {
  font-weight: 550;
}

.desktop {
  background-color: #F5F5F7;
  margin: 0;
  padding: 80px 0px 100px 0px;
  text-align: center;
}
.desktop__video {
  border-radius: 8px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  gap: 0;
}
.desktop__video video {
  max-width: 680px;
}

.assumptions {
  padding: 80px 0px;
}
.assumptions .assumptions__list {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.assumptions .limitation__wrapper {
  margin-top: 80px;
}

.problems {
  padding: 80px 0px;
}
.problems__items {
  display: flex;
  flex-direction: column;
  gap: 64px;
}
.problems__list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.problems__item-img {
  margin-bottom: 32px;
}
.problems h2 {
  text-align: center;
  margin-bottom: 40px;
}
.problems img {
  max-width: 680px;
  width: 100%;
}

@media (max-width: 680px) {
  .problems {
    padding-top: 40px;
  }
}
.mobile {
  background-color: #F5F5F7;
  padding: 80px 0px;
  text-align: center;
}
.mobile__video video {
  border-radius: 8px;
}

.logo {
  padding: 80px 0px;
}
.logo h2 {
  text-align: center;
}
.logo__content {
  display: flex;
  flex-direction: column;
  gap: 40px;
}
.logo__img {
  display: flex;
  flex-direction: column;
  gap: 13px;
}
.logo__img--main img {
  max-width: 680px;
  width: 100%;
}
.logo__img--small {
  display: flex;
  gap: 13px;
}
.logo__img--small img {
  max-width: 333px;
  width: 100%;
}
.logo__text {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.contact {
  padding: 80px 0px;
}

.contact__links {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.contact__links .cs-link {
  display: block;
}
.contact__links .cs-link::after {
  content: " " url(/img/cleaning/arrow-up-right.svg);
}